import Vue from 'vue';
import VueRouter from 'vue-router';
import login from '@/views/login/index.vue';
import register from '@/views/register/index.vue';
import main from '@/views/main/index.vue';
import addStu from '@/views/main/addStu/index.vue';
import stuList from '@/views/main/stuList/index.vue';
import notFound from '@/views/notFound/index.vue';
import Cookie from '@/util/cookie';

Vue.use(VueRouter);
const routes = [{
  path: '/notFound',
  component: notFound,
},
{
  path: '/login',
  component: login,
},
{
  path: '/register',
  component: register,
},
{
  path: '/main',
  component: main,
  redirect: '/main/stuList',
  children: [{
    path: 'addStu',
    name: 'addStu',
    component: addStu,
  },
  {
    path: 'stuList',
    name: 'stuList',
    component: stuList,
  },
  ],
},
];

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes,
  linkExactActiveClass: 'active',
});
// 路由守卫
router.beforeEach((to, from, next) => {
  // 若访问的是根路径则跳转到登录页面
  if (to.path === '/') {
    next('/login');
    return;
  }
  // notFound 路径对应的组件是否存在 若访问一个不存在的页面则跳转到notFound
  if (to.matched.length === 0) {
    next('/notFound');
    return;
  }
  // 进入主页面前要判断cookie是否存在 若存在则正常跳转否则跳转到登录页面
  if (to.matched[0].path === '/main') {
    const username = Cookie.getCookie('username');
    if (username) {
      next();
    } else {
      next('/login');
    }
  }
  next();
});

export default router;
