<template>
  <div id="student-add" class="student-add content">
    <form id="add-student-form">
      <div>
        <label for="add-name">姓名</label
        ><input type="text" id="add-name" name="name" v-model="stu.name"/>
      </div>
      <div>
        <label for="">性别</label>
        <input type="radio" name="sex" id="add-male" :value="0" checked v-model="stu.sex" />
        <label class="sex" for="add-male">男</label>
        <input type="radio" name="sex" id="add-female" :value="1" v-model="stu.sex" />
        <label class="sex" for="add-female">女</label>
      </div>
      <div>
        <label for="add-email">邮箱</label
        ><input type="text" id="add-email" name="email" v-model="stu.email"/>
      </div>
      <div>
        <label for="add-number">学号</label
        ><input type="text" id="add-number" name="sNo" v-model="stu.sNo"/>
      </div>
      <div>
        <label for="add-birth">出生年</label
        ><input type="text" id="add-birth" name="birth"  v-model="stu.birth"/>
      </div>
      <div>
        <label for="add-phone">手机号</label
        ><input type="text" id="add-phone" name="phone"  v-model="stu.phone"/>
      </div>
      <div>
        <label for="add-address">住址</label
        ><input type="text" id="add-address" name="address"  v-model="stu.address"/>
      </div>
      <div>
        <label for=""></label
        ><input type="submit" class="btn" id="add-form-btn" @click.prevent="commit"/>
        <input type="reset" class="btn" @click.prevent="reset"/>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      stu: {
        name: '',
        sex: '',
        email: '',
        sNo: '',
        birth: '',
        phone: '',
        address: '',
      },
    };
  },
  methods: {
    reset() {
      this.stu = {
        name: '',
        sex: '',
        email: '',
        sNo: '',
        birth: '',
        phone: '',
        address: '',
      };
    },
    async commit() {
      try {
        await this.$api.addStu(this.stu);
        this.$toast({ msg: '添加成功', type: 'success' });
      } catch (error) {
        this.$toast({ msg: error, type: 'fail' });
      }
    },
  },
};
</script>

<style scoped src='../index.css'>
</style>
